:root {
  --ins-color--orange: #ec7a08; }

button:focus {
  outline: none; }

.ins-c-remediation-modal .pf-c-modal-box__body > * + * {
  margin-top: 20px;
  margin-top: 1.25rem; }

.ins-c-remediation-modal .pf-c-check label {
  display: inline; }

.ins-c-remediation-modal .ins-m-text__accent {
  color: #c3c3c3; }

.ins-c-remediation-modal .ins-m-text__bold {
  font-weight: 500; }

.ins-c-remediation-modal table td {
  white-space: normal; }

.ins-c-remediation-modal .pf-c-label {
  background: var(--pf-global--Color--200); }
